//===========================================
// -Header start
//=========================================== 

.topcolors {
    width: 100%;
    display: flex;
    flex-wrap: wrap;

    div {
        width: 25%;
        height: 14px;
        background: #007DC6;
    }

    div:nth-child(2) {
        background: #F5821F;
    }

    div:nth-child(3) {
        background: #8AC33E;
    }

    div:nth-child(4) {
        background: #EE2E2A;
    }
}

.tagline {
    font-family: $font-default;
    background: #60C4FF;
    padding: 5px;
    color: #1E1E1E;
    font-size: 16px;
    text-align:center;

    a {
        text-decoration: underline;
        color: #1E1E1E;
        text-transform: uppercase;
    }
}

.toplinks {
    
    padding:15px 0 22px; 

    ul {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin:0;
        padding-bottom: 14px;
        border-bottom:1px solid #B6B6B6;

        li {
            list-style: none;

            a {
                display: block;
                text-decoration: none;
                color: #6A6A6A;
                font-size: 16px;
                border-right: 1px solid #6A6A6A;
                padding:0 12px;
            }

            &:last-child a {
                border:none;
            }
        }
    }
}

.logo-search {
    display: flex;  
    
    .logo {
        width: 120px;
    }
}

.topsearch-form {
    width: 100%;
    position: relative;
    padding: 0 0 0 20px;

    @include mq-only("xs", "sm") {
        padding: 0;
    }

    input[type="search"] {
        padding: 20px 15px 20px 45px;
        border-radius: 20px;
        background-image: url('/assets/images/search.png');
        background-repeat: no-repeat;
        background-position: 12px 9px;
    }
}

.mini-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    
    ul {
      width: 100%;
      padding-left: 0;
      text-align: right;
      margin-bottom: 0;
    }
  
    li {
      list-style: none;
      display: inline-block;
      margin:0 15px;

      a{
        display: block;
        text-decoration: none;
        color: #1E1E1E;
        font-size: 16px;
        padding:0 10px;
        font-weight: 600;
      }

      &:first-child {
        a{
            i{
                font-size:18px;
                margin-right: 3px;
            }
        }
      }

      &:last-child {
        a{
            border: 0;
        }
      }

      &.my-cart-nav{
       .cart-counter{
        position:absolute;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        font-size: 14px;
        font-weight: 600;
        background-color: $orrange;
        color:$whiteclr ;
        text-align: center;
        left: 20px;
        top: -9px;
       }
      }
    }
  }

.shopping-cart {

    position: absolute;
    z-index: 1;
    left: -22rem;
    background: #fff;
    border: 1px solid #efefef;
    top: 3rem;
    border-radius: 4px;
    display: none;

    .shopping-cart-items {
        min-width: 430px;
        display: -ms-flexbox;
        padding: 20px;
        max-height: 165px;
        overflow-y: scroll;
        
        .item-left {
        width: 100px;
        height: 100px;  
        }

        .item-right {
            h4 {
                font-family: $font-banner;
                font-size: 16px;
                line-height: 20px;
            } 
        }

        .cart-qty{

            margin-top: 10px;

            .input-group{
                outline: none;
                border: 1px solid $cart-input-border;
                border-radius: 8px;
                
                input{
                    border: none;
                    outline: none;
                    padding: 8px;
                    &:hover, &:focus, &:active{
                        border: none!important;
                    outline: none!important;
                    box-shadow: none;
                    }
                }
                button{
                    padding: 2px 16px;
                    font-size: 20px;
                }
            }
        }

        li {
            margin: 0 15px 20px;
            padding-bottom: 25px;
            border-bottom: 1px solid #efefef;

            &:last-child {
                border-bottom: 0;
                padding-bottom: 0;
            }
        }
    } 
    
   

    // Cart btn
    .ann-add-to-cart{
        background-color:$btngreen;
        padding: 8px 0px;
        gap: 8px;
    border-radius: 8px;
    color: $text-black;
    border: none;
    font-weight: 600;
    }
}
  

//===========================================
// -Header end
//===========================================
